import ylRequest from '../index'
import { IAccount } from './type'
import LocalCache from '../../utils/cache'
import userMenus from '@/assets/data/userMenus'
import userInfo from '@/assets/data/userInfo'

enum LoginAPI {
  AccountLogin = '/login'
}

export function accountLoginRequest(account: any) {
  // return ylRequest.post({
  //   url: LoginAPI.AccountLogin,
  //   data: account
  // })
  const userList = LocalCache.getCache('lists').userList.data.list
  const userInfo = userList.filter(function (item: any) {
    return item.name === account.name
  })[0]

  const roleList = LocalCache.getCache('lists').roleList.data.list
  const userMenus = roleList.filter(function (item: any) {
    return item.id === userInfo.roleId
  })[0].menuList
  console.log(userInfo, userMenus)

  const token = account.name + "'s token"
  const name = account.name

  LocalCache.setCache('token', token)
  LocalCache.setCache('userMenus', userMenus)
  LocalCache.setCache('userInfo', userInfo)

  // console.log(
  //   ylRequest.post({
  //     url: LoginAPI.AccountLogin,
  //     data: account
  //   })
  // )

  return {
    data: {
      token,
      name,
      userMenus,
      userInfo
    }
  }
}
